.container {
  width: 400px;
  margin: auto;
  text-align: center;
  background-color: white;
  box-shadow: 0px 0px 4px 3px rgb(196, 196, 196);
  padding: 44px;
  border-radius: 4px;
  margin-top: 55px;
  font-family: "Roboto", sans-serif;
}
.container h1 {
  min-width: 12.5rem;
  max-width: inherit;
  font-size: 3.125rem;
}
.container button {
  position: relative;
  margin-top: 10%;
  width: 100px;
  height: 36px;
  font-size: 21px;
  border: none;
  margin-inline: 11px;
  cursor: pointer;
  color: white;
  border-radius: 4px;
}

.start {
  background-color: rgb(5, 231, 5);
}
.stop {
  background-color: rgb(250, 16, 16);
}
.reset {
  background-color: rgb(250, 185, 8);
}

@media (min-width: 200px) and(max-width:480px) {
  .container {
    width: 100%;
  
  }
}
